* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #eff3f7;
  height: 100%;
}

html,
body,
#potato,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.header {
  width: 100%;
  height: 70px;
  display: flex;
  flex-direction: row;
}
.header > .headerleft,
.header > .headerright {
  display: flex;
}
.header > .headerleft > li,
.header > .headerright > li {
  margin-left: 20px;
  list-style: none;
}

.header > .headerleft {
  margin-left: 0;
  margin-right: auto;
}
.header > .headerleft > li {
  font-size: 1.5em;
}
.header > .headerright > li {
  font-size: 1.3em;
}
.header > .headerright > li > .vscsearch {
  font-size: 0.9em;
  position: absolute;
  margin-left: 5px;
  height: 30px;
}
.header > .headerright > li > input {
  padding-left: 30px;
  font-size: 1em;
}
.header > .headerright {
  margin-right: 0;
  margin-left: auto;
  padding-right: 50px;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.games {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  padding-top: 70px;
  width: 80%;
}

.games .game {
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.game img {
  position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0px 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.game .game_title,
.game .game__year {
  margin: 0;
  font-weight: 300;
}

.game .game_title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.game .game__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px 0;
}

.game__genres li {
  margin-right: 10px;
  font-size: 14px;
}
